import "./footer.scss";
import React from "react";
import logoBlack from "../../Assets/Images/logo back.png";
import appStore from "../../Assets/Images/Button - App Store.png";
import playMarket from "../../Assets/Images/Button - Android.png";
import { Link } from "react-router-dom";
import MyTree from "../../components/Tree/Tree";
import Garden from "../../components/Tree/AvtoTree/Garden";
import Realstate from "../../components/Tree/AvtoTree/Realstate";
import Private from "../../components/Tree/AvtoTree/Private";

export default function Footer() {
  return (
    <div className="footer">
      <div className="footer_top">
        <Link to={'/'}>
          <div className="footer_top_first">
            <img className="footer_top_first-img" src={logoBlack} alt="logo" />
            <p className="footer_top_first-text">
              Продай, найди, купи все что ты пожелаешь
            </p>
          </div>
        </Link>
        <p className="text">
          Веб сайт бесплатных обьявлений | на базе имеется 234 944 999 шт
          обяления
        </p>
        <div className="footer_top_secod">
          <Link to={'https://www.apple.com/ru/app-store/'}>
            <img
              className="footer_top_secod-img1"
              src={appStore}
              alt="App Store"
            />
          </Link>
          <Link to={'https://play.google.com/store/apps?hl=ru&gl=US'}>
            <img
              className="footer_top_secod-img2"
              src={playMarket}
              alt="Android"
            />
          </Link>
        </div>
      </div>
      <div className="footer_bottom">
        <MyTree />
        <Garden />
        <Realstate />
        <Private />
      </div>
    </div>
  );
}
