import "./login.scss";
import React, { useRef } from "react";
// import { useForm } from "react-hook-form";
import axios from "axios";
import kiviIcon from '../../Assets/Images/kivi_icon.png'

export default function Login({ setToken }) {
  // const initialValue = {
  //   login: "",
  //   password: "",
  // };
  // const {
  //   register,
  //   handleSubmit,
  //   formState: { errors },
  // } = useForm({
  //   defaultValues: initialValue,
  // });

  // const submit = (values) => {
  //   console.log(values);

  // };
  // console.log(errors);

  const URL = "https://reqres.in/api";
  const emailRef = useRef(null);
  const passwordRef = useRef(null);

  const elSubmit = (e) => {
    e.preventDefault();
    const info = {
      email: emailRef.current.value,
      password: passwordRef.current.value,
    };

    console.log(info);

    axios
      .post(`${URL}/login`, info)
      .then((res) => {
        if (res.status === 200) {
          localStorage.setItem("auth_token", res?.data?.token);
          setToken(res?.data?.token);
        }
      })
      .catch((err) => {
        alert(err);
      });
  };

  return (
    <div className="login">
      <form className="login_form" onSubmit={elSubmit}>
        <p className="login_form-text">Введите email или номер телефона</p>
        <input
          className="login_form-inpass"
          ref={emailRef}
          type="text"
          placeholder="login"
          // {...register("login", { minLength: 3 })}
        />
        <p className="login_form-text">Введите email или номер телефона</p>

        <input
          className="login_form-inpass"
          ref={passwordRef}
          type="text"
          placeholder="password"
          // {...register("password", { minLength: 6})}
        />

        <button className="login_form-button" type="submit">
          Войти
        </button>
      </form>

      {/* {(errors.login || errors.password) && <h2>Somethink went error</h2>} */}
      <div className="right">
        <div className="right_card">
          <img className="right_card-img" src={kiviIcon} alt="logo" />
          <p className="right_card-title">
            Откройте для себя целую плошадку для продаже
          </p>
          <button className="right_card-button">
          Как это делать?
          </button>
        </div>
      </div>
    </div>
  );
}
