import './tree.scss';
import React from 'react';
import { DownOutlined } from '@ant-design/icons';
import { Tree } from 'antd';
const treeData = [

  {
    title: 'Автомобили',
    key: '0-0',
    children: [
      {
        title: 'Легковые авто',
        key: '0-0-0',
      
      },
      {
        title: 'Мото',
        key: '0-0-1',
        
      },
      {
        title: 'Автобусы',
        key: '0-0-2',
        
      },
    ],
  },
];
const MyTree = () => {
  const onSelect = (selectedKeys, info) => {
    console.log('selected', selectedKeys, info);
  };
  return (
    <Tree className='tree'
      switcherIcon={<DownOutlined className='arrow' />}
      defaultExpandedKeys={['0-0-0']}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default MyTree;