import './product.scss';
import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link } from 'react-router-dom';


export default function Product() {
  const baseUrl = "https://fakestoreapi.com/products";

  const [products, setProduct] = useState([]);


  useEffect(() => {
    const fetchedProducts = async () => {
      const res = await axios.get(`${baseUrl}`);
      setProduct(res.data);
      console.log(res.data);
    };
    fetchedProducts();
  }, []);


  useEffect(() => {
    const showMore = async () => {
      const res = await axios.get(`${baseUrl}`);
      setProduct(res.data);
      console.log(res.data);
    };
    showMore();
  }, []);



  return (
    <section>
      <div className="product">
        {products.map((el) => {
          return (
            <Link className='product_card'  to={`/product/${el.id}`}>
            
            <article className='product_card-list' key={el.id}>
              <img
                alt="img"
                width={180}
                height={200}
                className="product_img"
                src={el.image}
                />
              <p className="product_title">{el.title}</p>
              <p className="product_category">{el.category}</p>
              <h3 className="product_price">{el.price}</h3>
            </article>
                </Link>
          );
        })}
      </div>

    </section>
  );
}


