import "./navbar.scss";
import React from "react";
import logo from "../../Assets/Images/favicon.png";
import { Link, useNavigate, } from "react-router-dom";
import { Button } from "antd";


export function Navbar({ token, setToken }) {
  const navigate = useNavigate();

  const handleLoginPage = () => {
    navigate("/login");
  };
  const handleProfilePage = () => {
    navigate("/profile");
  };





  return (
    <div className="navbar">
      <Link className="navbar_link" to={"/"}>
        <img className="navbar_logo" src={logo} alt="logo" />
        <p className="navbar_text">Продай, найди, купи все что пожелаешь…</p>
      </Link>
      <div className="navbar_el_wrapper">
        <Link className="navbar_el_link" to={"/"}>
          Объявления
        </Link>
        <Link className="navbar_el_link" to={"/"}>
          Магазины
        </Link>
        <Link className="navbar_el_link" to={"/"}>
          Для бизнеса
        </Link>
        <Link className="navbar_el_link" to={"/"}>
          Помощь
        </Link>
      </div>
      <div>
        <Button className="navbar_lang_button" type="text">
          Рус
        </Button>
        <Button className="navbar_lang_button" type="text">
          O’z
        </Button>
      </div>

      {token ? (
        <div className="profile">
          <p className="profile_text">Rakhmatillayev</p>
          <Button  onClick={handleProfilePage} className="profile_button" ></Button>
          {/* <div className="profile_dropdown">
            <button>log out</button>
          </div> */}
        </div>
      ) : (
        <div>
          <Button
            onClick={handleLoginPage}
            className="navbar_reg_button"
            type="link"
          >
            Вход
          </Button>
          <Button onClick={handleLoginPage} className="navbar_reg_button" type="link">
            Регистрация
          </Button>
        </div>
      )}
    </div>
  );
}
