import "../tree.scss";
import React from "react";
import { DownOutlined } from "@ant-design/icons";
import { Tree } from "antd";
const treeData = [
  {
    title: "Электроника",
    key: "0-0",
    children: [
      {
        title: "Телефоны",
        key: "0-0-0",
      },
      {
        title: "Компьютеры",
        key: "0-0-1",
      },
      {
        title: "Ноутбуки",
        key: "0-0-2",
      },
      {
        title: "планшеты ",
        key: "0-0-3",
      },
      {
        title: "Оргтехника и расходники ",
        key: "0-0-4",
      },
      {
        title: "ТВ,Фото,Видео и Аудио ",
        key: "0-0-5",
      },
      {
        title: "Игры и Приставки ",
        key: "0-0-6",
      },
      {
        title: "Техника для дома и Для себя ",
        key: "0-0-7",
      },
    ],
  },
];
const Realstate = () => {
  const onSelect = (selectedKeys, info) => {
    console.log("selected", selectedKeys, info);
  };
  return (
    <Tree
      className="tree"
      switcherIcon={<DownOutlined className="arrow_garden" />}
      defaultExpandedKeys={["0-0-0"]}
      onSelect={onSelect}
      treeData={treeData}
    />
  );
};
export default Realstate;
