import "./single-product.scss";

import { Link, useParams } from "react-router-dom";
import React, { useEffect, useState } from "react";
import axios from "axios";

export default function SingleProduct() {
  const { productId } = useParams();
  const [products, setProduct] = useState([]);

  useEffect(() => {
    const fetchedProducts = async () => {
      const res = await axios.get(
        `https://fakestoreapi.com/products/${productId}`
      );
      setProduct(res.data);
    };
    fetchedProducts();
  }, [productId]);

  return (
    <section className="el_product">
      <p className="el_product-title"> {products.title} </p>
      <div className="el_product-wrapper">
        <div className="el_product-image">
          <img width={400} height={500} src={products.image} alt={products.title} />
        </div>
        <div className="el_product-content">
          <p className="el_product-content-price"> {products.price} </p>
          <p className="el_product-content-des"> {products.description} </p>
          <p className="el_product-content-cat"> {products.category} </p>
        </div>
      </div>
      <Link className="el_product-button" to="/">
        Back
      </Link>
    </section>
  );
}
