import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom';


export default function Cards() {
    const [products, setProduct] = useState([]);

  useEffect(() => {
    const fetchedProducts = async () => {
     const res = await axios.get(`https://fakestoreapi.com/products`);
      setProduct(res.data);
    };
    fetchedProducts();
  }, []);

  return (
    <section className='el_product'>
       <div className="product">
        {products.map((el) => {
            if (el.category === 'jewelery') {
                
                return (
                  <Link className='product_card'  to={`/product/${el.id}`}>
                  
                  <article key={el.id}>
                    <img
                      alt="img"
                      width={180}
                      height={200}
                      className="product_img"
                      src={el.image}
                      />
                    <p className="product_title">{el.title}</p>
                    <p className="product_category">{el.category}</p>
                    <h3 className="product_price">{el.price}</h3>
                  </article>
                      </Link>
                );
            }
        })}
      </div>
    </section>
  )
}
