import './carusel.scss';
import React from 'react'
import plane from '../../Assets/Images/plane.png'
import house from '../../Assets/Images/house.png'
import comp from '../../Assets/Images/pc.png'
import personal from '../../Assets/Images/soc.png'
import garden from '../../Assets/Images/mountain.png'
import rem from '../../Assets/Images/building.png'
import hobby from '../../Assets/Images/gamepad.png'
import biznes from '../../Assets/Images/man.png'
import left from '../../Assets/Images/leftarrow.png'
import right from '../../Assets/Images/rightarrow.png'

export default function Carusel() {
  return (
    <div className='carusel_wrapper'>
        <div className="carusel_wrapper-item">
            <img src={left} alt="plane" />
        </div>
        <div className="carusel_wrapper-item">
            <img src={plane} alt="plane" />
            <h5 className='carusel_wrapper-item-text'>Автомобили</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={house} alt="plane" />
            <h5 className='carusel_wrapper-item-text'> Недвижимость</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={comp} alt="plane" />
            <h5 className='carusel_wrapper-item-text'>  Электроника</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={personal} alt="plane" />
            <h5 className='carusel_wrapper-item-text'>  Личные вещи</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={garden} alt="plane" />
            <h5 className='carusel_wrapper-item-text'>   Дом и Сад</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={rem} alt="plane" />
            <h5 className='carusel_wrapper-item-text'>   Стройка и ремонт</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={hobby} alt="plane" />
            <h5 className='carusel_wrapper-item-text'>    Хобби и Спорт</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={biznes} alt="plane" />
            <h5 className='carusel_wrapper-item-text'>     Бизнес и услуги</h5>
        </div>
        <div className="carusel_wrapper-item">
            <img src={right} alt="plane" />
        </div>
    </div>
  )
}
