import './layout.scss';
import React from "react";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";

export function Layout({ children, setToken, token}) {
  return (
    <>
    <div className="container">
      
      <Header token={token} setToken={setToken} />
      <div>
        {children}
      </div>
    </div>
      <Footer />
    </>
  );
}
